@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

body{
    margin: 0;
    padding: 0;
}

//css for deel
.deel1{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: flex-start;
    justify-content: space-between;
}

.deel1 a{
    text-decoration: none;
}

.deel1 .donate-div{
    display: flex;
    align-items: center;
    border: 2px solid black;
    padding: 0.3vw;
}

.deel1 .donate-div a{
    margin-left: 0.2vw;
}

.nav{
    display: flex;
    flex-direction: row;
    border: 1px solid black;
}

.nav a{
  width: 15vw;
}

.nav img{
    width: 5vw;
    height: 5vw;
}


.dropdown{
    display: flex;
    flex-direction: column;
    text-align: center;
}

.dropdown-content{
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
}